import React, { useState, useEffect } from 'react';
import gameData from './gameData.json';
import { LineChart, Line, XAxis, YAxis, CartesianGrid, Tooltip, Legend, ResponsiveContainer, BarChart, Bar } from 'recharts';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faChartLine, faUser, faList } from '@fortawesome/free-solid-svg-icons';

const data = gameData

const App = () => {
  const [activeTab, setActiveTab] = useState('summary');
  const [expandedGames, setExpandedGames] = useState({});

  const groupedGames = data.reduce((acc, game) => {
    if (!acc[game.Date]) {
      acc[game.Date] = [];
    }
    acc[game.Date].push(game);
    return acc;
  }, {});

  const toggleGame = (date) => {
    setExpandedGames(prev => ({...prev, [date]: !prev[date]}));
  };

  const playerStats = data.reduce((acc, game) => {
    if (!acc[game.Player]) {
      acc[game.Player] = {
        gamesPlayed: 0,
        totalPlayersPlaying: 0,
        totalBuyIn: 0,
        totalBuyBack: 0,
        totalMoneyIn: 0,
        totalMoneyOut: 0,
        totalWinnings: 0,
        return: 0,
      };
    }
    acc[game.Player].gamesPlayed++;
    acc[game.Player].totalPlayersPlaying += parseInt(game["Players Playing"]);
    acc[game.Player].totalBuyIn += parseFloat(game["Money In"]);
    acc[game.Player].totalBuyBack += parseFloat(game["Buy in "]);
    acc[game.Player].totalMoneyIn += parseFloat(game["Total Money In"]);
    acc[game.Player].totalMoneyOut += parseFloat(game["Total Money Out"]);
    acc[game.Player].totalWinnings += parseFloat(game.Winnings);
    acc[game.Player].return += parseFloat(game["Return"]);
    return acc;
  }, {});

  const playerStatsArray = Object.entries(playerStats).map(([player, stats]) => ({
    player,
    gamesPlayed: stats.gamesPlayed,
    avgPlayersPlaying: (stats.totalPlayersPlaying / stats.gamesPlayed).toFixed(2),
    avgBuyIn: (stats.totalBuyIn / stats.gamesPlayed).toFixed(2),
    avgBuyBack: (stats.totalBuyBack / stats.gamesPlayed).toFixed(2),
    avgTotalIn: (stats.totalMoneyIn / stats.gamesPlayed).toFixed(2),
    avgTotalOut: (stats.totalMoneyOut / stats.gamesPlayed).toFixed(2),
    totalWinnings: stats.totalWinnings.toFixed(2),
    winningsPerGame: (stats.totalWinnings / stats.gamesPlayed).toFixed(2),
  }));

  const winningsTrendData = Object.entries(groupedGames).map(([date, games]) => ({
    date,
    ...games.reduce((acc, game) => {
      acc[game.Player] = parseFloat(game.Winnings);
      return acc;
    }, {}),
  }));

  return (
<div className="min-h-screen bg-gray-100 p-4">
  <h1 className="text-2xl font-bold mb-4 text-center">Poker Stats</h1>
  <div className="fixed inset-x-0 bottom-0 bg-white shadow-[0_-4px_10px_rgba(0,0,0,0.15)] z-50 text-gray-800">
    <div className="max-w-screen-xl mx-auto p-4 flex justify-around">
      <button
        className={`px-4 py-2 rounded ${activeTab === 'summary' ? 'bg-blue-500 text-white' : 'bg-gray-200'}`}
        onClick={() => setActiveTab('summary')}
      >
        <FontAwesomeIcon icon={faList} />
      </button>
      <button
        className={`px-4 py-2 rounded ${activeTab === 'stats' ? 'bg-blue-500 text-white' : 'bg-gray-200'}`}
        onClick={() => setActiveTab('stats')}
      >
        <FontAwesomeIcon icon={faUser} />
      </button>
      <button
        className={`px-4 py-2 rounded ${activeTab === 'viz' ? 'bg-blue-500 text-white' : 'bg-gray-200'}`}
        onClick={() => setActiveTab('viz')}
      >
        <FontAwesomeIcon icon={faChartLine} />
      </button>
    </div>
  </div>


      {activeTab === 'summary' && (
        <div>
          {Object.entries(groupedGames).map(([date, games]) => (
            <div key={date} className="mb-4 bg-white rounded-lg shadow p-4">
              <h2 
                className="text-lg font-semibold cursor-pointer" 
                onClick={() => toggleGame(date)}
              >
                {date} - Game {games[0]["Game Number"]} - {games[0].Location} - {games[0]["Players Playing"]} Players
              </h2>
              {expandedGames[date] && (
                <div className="mt-2">
                  {games.map((game, index) => (
                    <div 
                      key={index} 
                      className={`p-2 rounded ${parseFloat(game.Winnings) > 0 ? 'bg-green-100' : 'bg-red-100'}`}
                    >
                      <p>{game.Player}: ${game.Winnings}</p>
        
                      <p>Buy In: ${game["Money In"]}</p>
                      <p>Buy Back: ${game["Buy in "]}</p>
                      <p>Total Money In: ${game["Total Money In"]}</p>
                      <p>Total Money Out: ${game["Total Money Out"]}</p>
                      <p>return: {`${(game["Return"] * 100).toFixed(2)}%`}</p>

                    </div>
                  ))}
                </div>
              )}
            </div>
          ))}
        </div>
      )}

      {activeTab === 'stats' && (
        <div className="overflow-x-auto">
          <table className="min-w-full bg-white">
            <thead>
              <tr>
                <th className="px-4 py-2">Player</th>
                <th className="px-4 py-2">Games Played</th>
                <th className="px-4 py-2">Avg Players</th>
                <th className="px-4 py-2">Avg Buy In $</th>
                <th className="px-4 py-2">Avg Buy Back $</th>
                <th className="px-4 py-2">Avg Total $ In</th>
                <th className="px-4 py-2">Avg Total $ Out</th>
                <th className="px-4 py-2">Total Winnings</th>
                <th className="px-4 py-2">Winnings/Game</th>
              </tr>
            </thead>
            <tbody>
              {playerStatsArray.map((stats, index) => (
                <tr key={index} className={index % 2 === 0 ? 'bg-gray-50' : 'bg-white'}>
                  <td className="px-4 py-2">{stats.player}</td>
                  <td className="px-4 py-2">{stats.gamesPlayed}</td>
                  <td className="px-4 py-2">{stats.avgPlayersPlaying}</td>
                  <td className="px-4 py-2">${stats.avgBuyIn}</td>
                  <td className="px-4 py-2">${stats.avgBuyBack}</td>
                  <td className="px-4 py-2">${stats.avgTotalIn}</td>
                  <td className="px-4 py-2">${stats.avgTotalOut}</td>
                  <td className="px-4 py-2">${stats.totalWinnings}</td>
                  <td className="px-4 py-2">${stats.winningsPerGame}</td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      )}

      {activeTab === 'viz' && (
        <div>
          <h2 className="text-xl font-semibold mb-4">Winnings Trend</h2>
          <ResponsiveContainer width="100%" height={300}>
            <LineChart data={winningsTrendData}>
              <CartesianGrid strokeDasharray="3 3" />
              <XAxis dataKey="date" />
              <YAxis />
              <Tooltip />
              <Legend />
              {Object.keys(playerStats).map((player, index) => (
                <Line 
                  key={player} 
                  type="monotone" 
                  dataKey={player} 
                  stroke={`hsl(${index * 30}, 70%, 50%)`} 
                  strokeWidth={2}
                />
              ))}
            </LineChart>
          </ResponsiveContainer>

          <h2 className="text-xl font-semibold my-4">Total Winnings by Player</h2>
          <ResponsiveContainer width="100%" height={300}>
            <BarChart data={playerStatsArray}>
              <CartesianGrid strokeDasharray="3 3" />
              <XAxis dataKey="player" />
              <YAxis />
              <Tooltip />
              <Bar dataKey="totalWinnings" fill="#8884d8" />
            </BarChart>
          </ResponsiveContainer>
        </div>
      )}
    </div>
  );
};

export default App;